.selected {
  border-top: 1px solid var(--color-divider-v2);
}
.selected__top_inner {
  align-items: center;
  display: flex;
  font-size: 16px;
  font-weight: 700;
  justify-content: space-between;
  padding: 10px 16px;
}

.selected__clean_filter {
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
  text-transform: none;
}

.selected__clean_filter:hover {
  text-decoration: underline;
}

.selected__Button {
  background-color: var(--color-dark-button);
  border: none;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  margin: 5px;
  min-height: 36px;
  padding: 10px 26px 10px 12px;
  position: relative;
  white-space: nowrap;
  cursor: pointer;
}

.selected__Button:hover {
  opacity: 0.7;
}

.selected__Button:after,
.selected__Button:before {
  background-color: var(--color-white);
  border-radius: 0.5px;
  content: '';
  height: 1px;
  position: absolute;
  right: 8px;
  top: 50%;
  width: 12px;
}

.selected__Button:after {
  transform: rotate(-45deg);
}

.selected__Button:before {
  transform: rotate(45deg);
}

.selected__list_buttons {
  padding: 5px 11px;
}
