.button {
  background-color: var(--color-dark);
  cursor: pointer;
  font-size: 16px;
  line-height: 1.4;
  padding: 10px 16px 10px 36px;
  display: flex;
  align-items: center;
}

.button:hover .button__title {
  text-decoration: underline;
}

.button__count {
  margin-left: 0.5ch;
  color: var(--color-gray-middle);
}

.wrapper {
  display: flex;
  justify-content: space-between;
}
.arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 9px;
  padding: 0;
  width: 42px;
  background-color: var(--color-dark);
  cursor: pointer;
}
.arrow__svg {
  fill: var(--color-gray-middle);
  transform: rotate(-90deg) translateX(2px);
  transition: transform 0.15s ease-in-out, fill 0.15s ease-in-out;
}
.arrow__svg.active {
  transform: rotate(90deg) translateX(2px);
}

.items {
  background-color: var(--color-dark-button);
}

.item__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.item__button {
  background-color: initial;
  cursor: pointer;
  font-size: 16px;
  line-height: 1.4;
  padding: 10px 16px 10px 54px;
  position: relative;
  display: flex;
  align-items: center;
}

.item__button:hover .button__title {
  text-decoration: underline;
}

.item__button_svg {
  left: 34px;
  position: absolute;
  fill: var(--color-white);
}
.item__add {
  /* background-color: var(--color-dark); */
  border-radius: 50%;
  height: 22px;
  width: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
  cursor: pointer;
}
.item__add_svg {
  fill: red;
  /* fill: var(--color-white); */
  flex-shrink: 0;
  transition: transform 0.15s ease-in-out;
}
.item__add_svg.active {
  transform: rotate(45deg);
}

.item__add_pos_svg {
  left: 14px;
  position: absolute;
}
