.button {
  border-top: 1px solid var(--color-divider-v2);
  background-color: var(--color-dark-button);
  cursor: pointer;
  display: grid;
  grid-template-columns: 1fr 3fr 20px;
  padding: 16px 14px 16px 16px;
  position: relative;
  transition: opacity 0.15s linear;
  width: 100%;
  align-items: center;
}

.button__title {
  font-weight: 700;
  grid-column-end: 2;
  grid-column-start: 1;
  text-align: left;
  font-size: 16px;
  white-space: nowrap;
}
.button__sub_title {
  color: var(--color-gray-middle);
  grid-column-end: 3;
  grid-column-start: 2;
  margin-left: auto;
  max-width: 75%;
  overflow: hidden;
  padding: 0 12px 0 24px;
  pointer-events: none;
  text-align: right;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.button__svg {
  fill: var(--color-gray-middle);
  grid-column-end: 4;
  grid-column-start: 3;
  transform: rotate(-90deg) translateX(2px);
  transition: transform 0.15s ease-in-out, fill 0.15s ease-in-out;
}
.button__svg.active {
  transform: rotate(90deg) translateX(2px);
}
