.BtnClose__wrapper {
  align-items: center;
  background-color: var(--color-dark-button);
  display: flex;
  font-size: 18px;
  font-weight: 900;
  height: 58px;
  justify-content: space-between;
  padding: 0 16px;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-white);
}

.BtnClose__svg {
  fill: var(--color-white);
}

.BtnClose__svg_inner {
  cursor: pointer;
  padding: 8px;
  display: flex;
}
